
.spark-path {
  stroke-width: 1;
  fill: none;
}
.svg-container {
    display: inline-block;
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    vertical-align: top;
    overflow: visible;
}
.svg-content {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
}
#sCanvas{
  display: inline-block;
}




